import React from "react"

import { graphql } from "gatsby"
import Img from "gatsby-image"

import galleryStyles from "../styles/gallery.module.css"
import Header from "../components/Header"
import NavBar from "../components/NavBar"
import Footer from "../components/Footer"

export default function gallery({ data }) {
  var NumberofColumns = 4
  var Slices = []
  var Length = data.GalleryImages.edges.length
  var ItemsPerColumn = Math.floor(Length / NumberofColumns)
  var Remainder = Length - ItemsPerColumn * NumberofColumns
  var startIndex = 0

  for (var i = 0; i < NumberofColumns; i++) {
    var Incrementer = ItemsPerColumn
    if (i + 1 <= Remainder) Incrementer = ItemsPerColumn + 1
    Slices[i] = data.GalleryImages.edges.slice(
      startIndex,
      startIndex + Incrementer
    )
    startIndex += Incrementer
  }

  return (
    <React.Fragment>
      <NavBar />
      <Header style={{ paddingTop: "4rem" }}>Gallery</Header>
      <div className={galleryStyles.container}>
        {Slices.map((item, index) => {
          return (
            <div key={index} className={galleryStyles.column}>
              {item.map(nodes => {
                return (
                  <Img
                    key={nodes.node.id}
                    className={galleryStyles.image}
                    fluid={nodes.node.childImageSharp.fluid}
                  />
                )
              })}
            </div>
          )
        })}
      </div>
      <Footer />
    </React.Fragment>
  )
}

export const query = graphql`
  query {
    GalleryImages: allFile(
      filter: {
        sourceInstanceName: { eq: "Images" }
        relativeDirectory: { eq: "Gallery" }
        extension: { eq: "jpg" }
        childImageSharp: { fluid: {} }
      }
      sort: { fields: birthTime, order: DESC }
    ) {
      edges {
        node {
          id
          childImageSharp {
            fluid(maxWidth: 300, quality: 100) {
              originalName
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`
